/* make variables */
:root {
  --lightgrey: #D9D9D9;
  --grey: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor: #3c3f45;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}

::-webkit-scrollbar {
  display: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.stroke-text {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
}

.btn-orange {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 40px;
  border: none;
  color: #fff;
  background-color: var(--orange);
  padding: 0.5rem;
  font-weight: bold;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.btn-orange:hover {
  background-color: #d97911;
}

.btn-outline {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 40px;
  color: #fff;
  background-color: transparent;
  border: 2px solid var(--orange);
  padding: 0.5rem;
  font-weight: bold;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.btn-outline:hover {
  background-color: var(--orange);
}

.btn-white {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 40px;
  border: none;
  color: #000;
  background-color: #fff;
  padding: 0.5rem;
  font-weight: bold;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.btn-white:hover {
  background-color: #e3e3e3;
}

.blur {
  position: absolute;
  background: rgba(253,120,43,0.69);
  border-radius: 50%;
  filter: blur(150px);
  z-index: -9;
}